<template>
    <div>
        <div class="container mt-5">
            <div class="row">
                <div class="col-12">
                    <div id="search">
                        <input type="text" @input="searchBy" v-model="searchText" class="form-control" id="searchBox" autocomplete="off" placeholder="نام فیلم یا آیدی IMDB">
                    </div>
                </div>
            </div>
        </div>
        <loading v-if="loading && searchText"></loading>
       <div class="container mt-4">
           <div class="row">
               <div class="col-md-3 col-6" v-for="(item, index) in searchResult" :key="index">
                   <router-link :to="`/t/${item.id}`">
                       <movie-box :title="item.name" :subtitle="item.sub" :year="item.year" :poster="item.poster"></movie-box>
                   </router-link>
               </div>
           </div>
       </div>
    </div>
</template>

<script>
    let timeout = null;
    export default {
        name: 'Home',
        data () {
            return {
                loading : false,
                searchResult: {},
                searchText : null
            }
        },
        methods : {
            searchBy () {
                clearTimeout(timeout);
                let self = this;
                self.loading = true;
                self.searchResult = {};
                timeout = setTimeout(() => {
                    this.$http.get(`s/${self.searchText.toLowerCase()}`)
                        .then(function (response) {
                            self.searchResult = response.data;
                            self.loading = false;
                        })
                        .catch(function (error) {
                            console.log(error);
                            self.loading = false;
                        });
                }, 300);
            }
        },
    }
</script>

