<template>
    <div class="container pb-5">
        <loading v-if="loading"></loading>
        <div v-if="!loading" class="row title_content mt-5">
            <div class="col-md-4">
                <img v-lazy="title.poster" :alt="title.title" class="img-fluid rounded poster">
            </div>
            <div class="col-md-8">
                <h1>{{title.title}}</h1>
                <ul class="title_details">
                    <li>Year: <span>{{title.year}}</span></li>
                    <li>Rating: <span>{{title.ratingValue}}/10</span></li>
                    <li>Rating count: <span>{{title.ratingCount}}</span></li>
                    <li>Director: <span>{{title.director}}</span></li>
                    <li>Writer: <span>{{title.writer}}</span></li>
                    <li>Stars: <span>{{title.stars}}</span></li>
                    <li>Summery: <span>{{title.summery}}</span></li>
                    <li>Genre: <span>{{title.genre}}</span></li>
                    <li>Time: <span>{{title.time}}</span></li>
                    <li><a target="_blank" :href="`https://www.imdb.com/title/${title.id}`"><img src="https://ia.media-imdb.com/images/M/MV5BODc4MTA3NjkzNl5BMl5BcG5nXkFtZTgwMDg0MzQ2OTE@._V1_.png" alt="imdb" width="40"></a></li>
                </ul>
                <h5 class="storyline mt-3">Storyline :</h5>
                <p class="storyline_content">{{title.storyline}}</p>
            </div>
        </div>
        <div v-if="!loading" class="row title_download mt-3">
           <div v-for="(item, index) in title.download" :key="index" class="mb-3 col-12">
               <b-button block href="#" v-b-toggle="`accordion-${index}`" variant="secondary">{{item.title}}</b-button>
               <b-collapse :id="`accordion-${index}`" accordion="my-accordion" role="tabpanel">
                   <div v-for="(links, itemIndex) in item.links" :key="itemIndex">
                       <b-button block href="#" v-b-toggle="`download-${itemIndex}`" class="mt-2" variant="primary">{{links.title}}</b-button>
                       <b-collapse :id="`download-${itemIndex}`" accordion="my-accordion2" role="tabpanel">
                           <ul class="download_item">
                               <li v-for="(link, linksIndex) in links.link" :key="linksIndex">
                                   <div class="row pb-3">
                                       <div class="col" v-if="link.quality">{{link.quality}}</div>
                                       <div class="col" style="direction: ltr" v-if="link.size">{{link.size}} :حجم</div>
                                       <div class="col" v-if="link.encoder">انکودر : {{link.encoder}}</div>
                                   </div>
                                   <div class="row">
                                       <div class="col">
                                           <a :href="link.downloadLink" target="_blank" class="btn btn-success btn-block">لینک دانلود</a>
                                       </div>
                                       <div class="col" v-if="link.qualityImage">
                                           <a :href="link.qualityImage" target="_blank" class="btn btn-info btn-block">نمونه کیفیت</a>
                                       </div>
                                   </div>
                               </li>
                           </ul>
                       </b-collapse>
                   </div>
               </b-collapse>
           </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Title",
        data () {
            return {
                loading : false,
                title: {}
            }
        },
        methods: {
            getTitle () {
                let self = this;
                self.loading = true;
                this.$http.get(`t/${this.$route.params.id}`)
                    .then(function (response) {
                        self.title = response.data;
                        document.title = response.data.title;
                        self.loading = false;
                    })
                    .catch(function (error) {
                        console.log(error);
                        self.loading = false;
                    });
            }
        },
        created () {
            this.getTitle();
        }
    }
</script>

