import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import Title from '@/views/Title'

Vue.use(Router);
export default new Router({
    // mode: 'history',
    base: process.env.NODE_ENV === 'development' ? '/' : '/imdbDownloader/',
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/t/:id',
            name: 'title',
            component: Title
        }
    ]
})
